

.rootHero, .loginPage, .signupPage {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;


    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    .heroHeader {
        display: flex;
        gap: 0rem;
        align-items: center;
        justify-content: center;
        padding-right: 1.6rem;
        margin-bottom: -1rem;

        p {
            margin-top: 3.8rem;
            font-size: 2rem;
            font-weight: 700;

            span {
                font-weight: 300;
            }
        }

        img {
            width: 7.5rem;
        }
    }

    .heroSubHeader {
        // padding-left: .8rem;
        width: 20rem;
        text-align: center;
        font-size: .85rem;
    }


    .heroActions {
        display: flex;
        gap: 1rem;
        margin-top: 2rem;

        .login {
            background: #3E58BE;
            color: white;
        }

        .signup {
            background: #3e58be31;
            color: #3E58BE;
        }
    }
}